* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.infoCartTitleConteiner {
  display: flex;
}

.infoCartImage {
  height: 100px;
  width: 100px;
}

.infoCartTitle {
  font-size: 30px;
  margin-left: 10px;
}

.countryListItem {
  display: flex;
  padding: 10px;
}

.countryListImage {
  height: 70px;
  width: 100px;
}

.countryListText {
  padding: 10px;
}
